import React, { Component } from "react";
import "./App.css";
import firebase from "./firebase.js";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCommentAlt } from "@fortawesome/free-solid-svg-icons";
import Header from "./Header.js";
import Footer from "./Footer.js";
import axios from "axios";
import ToggleButton from "react-bootstrap/ToggleButton";
import Switch from "@material-ui/core/Switch";

class App extends Component {
  constructor(props) {
    super(props);
    //this.state is the initial values
    this.state = {
      messages: [],
      userInput: "",
      userMessage: "",
      userName: "",
      placeHolder: "e.g. Jeannette",
      color: "",
      theme: "#171717",
      themeColor: { border: "white", chatMessage: "#1c1c1e" },
      text: "Light Mode",
      filteredMessage: "",
      placeHolderMessage: "message",
      errorMessage: "",
      fontColor: "white"
    };
    this.myRef = React.createRef();
  }

  componentDidMount() {
    //getting entire database.When the value changes on the database, it will get response value
    const dbRef = firebase.database().ref("messages");
    dbRef.on("value", response => {
      if (response.val() === null) {
        return;
      }
      let data = Object.values(response.val());
      this.setState(
        {
          messages: data
        },
        this.scroll
      );
    });
  }

  //api Call
  callApi = message => {
    return axios
      .get("https://www.purgomalum.com/service/json", {
        params: {
          text: message,
          fill_char: "*"
        }
      })
      .then(response => {
        let responses = response.data.result;
        if (typeof responses != "string") {
          console.log("error");
        } else {
          let stringArray = responses.replace(/\*/g, "ðŸ’©");
          console.log(stringArray, "stringarray");
          return stringArray;
        }
      });
  };

  //handleChange it will store the information on firebase. When I'm typing, it will be added to firebase
  handleChange = e => {
    e.preventDefault();
    this.setState({ userInput: e.target.value });
  };
  handleMessageChange = e => {
    e.preventDefault();
    let value = e.target.value;
    this.setState({ userMessage: value });
    console.log(this.state.userMessage);
  };

  handleInput = () => {
    if (this.state.name === " ") {
      this.setState({ errorMessage: "Type Username" });
    } else {
      this.setState({ userName: "Message" });
    }
  };
  handleSubmit = e => {
    e.preventDefault();
    this.setState(
      {
        name: this.state.userInput
      },
      () => {
        this.handleInput();
      }
    );
  };
  handleSubmitMessageForm = async e => {
    e.preventDefault();
    //go to database, add the this.state userinput into the database. change the userinput as blank
    const dbRef = firebase.database().ref("messages");
    let newValue = await this.callApi(this.state.userMessage);
    dbRef.push({
      content: newValue,
      name: this.state.userInput,
      dateCreated: new Date().toLocaleString("en-US"),
      color: this.state.color,
      fontColor: this.state.fontColor
    });
    this.setState({ userMessage: "" });
  };
  componentDidUpdate() {
    this.scroll();
  }
  scroll = () => {
    const node = this.myRef.current;
    node.scrollTop = node.scrollHeight;
  };

  handleColor = event => {
    this.setState({ color: event.target.value });
  };

  handleTheme = () => {
    this.setState(prevState =>
      prevState.theme === "white"
        ? {
            theme: "#171717",
            themeColor: { border: "lightgray", chatMessage: "#1c1c1e" },
            text: "Light Mode",
            fontColor: "white",
            color: this.handleLightColor()
          }
        : {
            theme: "white",
            themeColor: { border: "lightgray", chatMessage: "#e6e4ec" },
            text: "Dark Mode",
            fontColor: "black",
            color: this.handleDarkColor()
          }
    );
  };

  // If this.state.color is lightcolor, change this.state.color to darker color when the state is light.
  handleDarkColor = () => {
    if (this.state.color === "#ab9df2") {
      this.setState({ color: "#6900F2" });
    } else if (this.state.color === "#f09665") {
      this.setState({ color: "#A14B12" });
    } else if (this.state.color === "#a9dc76") {
      this.setState({ color: "#325115" });
    } else {
      this.setState({ color: "#901437" });
    }
  };
  handleLightColor = () => {
    if (this.state.color === "#6900F2") {
      this.setState({ color: "#ab9df2" });
    } else if (this.state.color === "#A14B12") {
      this.setState({ color: "#f09665" });
    } else if (this.state.color === "#325115") {
      this.setState({ color: "#a9dc76" });
    } else {
      this.setState({ color: "#ea5d86" });
    }
  };

  // If this.state.color is lightcolor, change this.state.color to darker color when the state is light.
  getDarkColor = color => {
    if (color === "#ab9df2" || color === "#6900F2") {
      return "#6900F2";
    } else if (color === "#f09665" || color === "#A14B12") {
      return "#A14B12";
    } else if (color === "#a9dc76" || color === "#325115") {
      return "#325115";
    } else {
      return "#901437";
    }
  };
  getLightColor = color => {
    if (color === "#6900F2" || color === "#ab9df2") {
      return "#ab9df2";
    } else if (color === "#A14B12" || color === "#f09665") {
      return "#f09665";
    } else if (color === "#325115" || color === "#a9dc76") {
      return "#a9dc76";
    } else {
      return "#ea5d86";
    }
  };

  render() {
    return this.state.userName === "" ? (
      <div className="firstPage">
        <div className="formContainer">
          <form
            action="submit"
            onSubmit={this.handleSubmit}
            ref={this.myRef}
            className="firstPageForm"
            autocomplete="off"
          >
            <h1>ChatTime</h1>
            <p>Join HackerYou Chat room</p>
            <p>Enter Username</p>
            <div className="enterUserName">
              <label htmlFor="userName" className="visuallyHidden">
                UserName
              </label>
              <input
                value={this.state.userInput}
                type="text"
                id="userName"
                placeholder={this.state.placeHolder}
                onChange={this.handleChange}
                maxLength="15"
                required
                autocomplete="off"              />
              {/* <p>{this.state.errorMessage}</p> */}
            </div>
            <p>Select Username Color</p>
            <div className="themeColorSelection">
              <input
                type="radio"
                value="#f09665"
                name="color"
                id="orange"
                onClick={this.handleColor}
                required
                // checked

              />
              <label htmlFor="orange" />

              <input
                type="radio"
                value="#ab9df2"
                name="color"
                id="purple"
                onClick={this.handleColor}
              />

              <label htmlFor="purple" />
              <input
                type="radio"
                value="#ea5d86"
                name="color"
                id="PaleVioletRed"
                onClick={this.handleColor}
              />
              <label htmlFor="PaleVioletRed" />

              <input
                type="radio"
                value="#a9dc76"
                name="color"
                id="lightGreen"
                onClick={this.handleColor}
              />
              <label htmlFor="lightGreen" />
            </div>
            <button type="submit" className="cta-1">
              <p>Let's Chat</p>
              <FontAwesomeIcon icon={faCommentAlt} />
            </button>
          </form>
        </div>
        <Footer />
      </div>
    ) : (
      <div className="App" style={{ background: this.state.theme }}>
        <div className="heading">
          <div className="container">
            <Header text={this.state.userInput} color={this.state.color} />
            <div className="toggleSwitch">
              <p style={{ color: this.state.fontColor }}>Dark Mode</p>
              <div className="switch">
                <Switch
                  checked={this.state.theme === "white"}
                  onChange={this.handleTheme}
                  value="checkedA"
                  color="primary"
                />
              </div>
              <p style={{ color: this.state.fontColor }}>Light Mode</p>
            </div>
          </div>
        </div>
        <div className="container">
          <div
            className="chatMessages"
            ref={this.myRef}
            style={{ border: `1px solid ${this.state.themeColor.border}` }}
          >
            {this.state.messages.map((message, i) => {
              return (
                <div className="chatmessages" key={i}>
                  <div
                    className="chat"
                    style={{
                      background: this.state.themeColor.chatMessage
                    }}
                  >
                    <p
                      className="name"
                      style={{
                        color:
                          this.state.theme === "white"
                            ? this.getDarkColor(message.color)
                            : this.getLightColor(message.color)
                      }}
                    >
                      {message.name}
                    </p>
                    <p
                      className="content"
                      style={{ color: this.state.fontColor }}
                    >
                      {message.content}
                    </p>
                    <p
                      className="dateCreated"
                      style={{
                        color:
                          this.state.theme === "white"
                            ? this.getDarkColor(message.color)
                            : this.getLightColor(message.color)
                      }}
                    >
                      {message.dateCreated}
                    </p>
                  </div>
                </div>
              );
            })}
          </div>
          <form
            action="submit"
            onSubmit={this.handleSubmitMessageForm}
            id="messageInputArea"
          >
            <textarea
              value={this.state.userMessage}
              type="text"
              placeholder={this.state.placeHolderMessage}
              onChange={this.handleMessageChange}
              id="messageInput"
              required
              data-gramm_editor="false"
              style={{
                border: `1px solid ${this.state.themeColor.border}`
              }}
            />
            <button
              type="submit"
              className="cta-messageInputArea"
              style={{
                border: `1px solid ${this.state.themeColor.border}`
              }}
            >
              <FontAwesomeIcon icon={faCommentAlt} />
            </button>
          </form>
        </div>
        <footer style={{ background: this.state.color }}>
          <p>&copy; Jeannette Ng 2019</p>
        </footer>
      </div>
    );
  }
}
export default App;
